export class Opinion {
    name: string;
    lastName: string;
    email: string;
    opinion: string;

    constructor(name: string, lastName: string, email: string, opinion: string) {
        this.name = name;
        this.lastName = lastName;
        this.email = email;
        this.opinion = opinion
    }
}


